import {request} from "@/network/request";


/**
 *  
 * @param query
 *              {
 *                userId: Number, id
 *                groupId: Number, id
 *                page: Number,     
 *                limit: Number     
 *              }
 *
 * @returns {AxiosPromise}
 */
export function queryHistoryGroupInformNetwork(query){
  return request({
    url: '/inform/pageQueryInformRecord',
    data: query,
    method: 'post'
  })
}

/**
 * 
 *             {
 *                userId: Number, id
 *                groupId: Number, id
 *              }
 * @param query
 * @returns {AxiosPromise}
 */
export function queryInformIn3dayNetwork(query){
  return request({
    url: '/inform/queryInformIn3day',
    data: {
      userId: query.userId,
      groupId: query.groupId
    },
    method: 'post'
  })
}


/**
 * 
 * @param userId
 * @param groupId
 * @param notReadList
 * @returns {AxiosPromise}
 */
export function clearNotReadNetWork(userId, groupId, notReadList){
  return request({
    url: '/inform/clearNotRead',
    data: {
      userId,
      groupId,
      notReadList
    },
    method: 'post'
  })
}